@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap");

* {
  overflow-x: hidden;
  font-family: Inter;
  line-height: 150%;
}

.d-flex {
  display: flex;
}

.d-grid {
  display: grid;
}

.td-none {
  text-decoration: none;
  color: black;
}

.p-relative {
  position: relative;
}

.navbar-container {
  width: -webkit-fill-available;
  height: 80px;
  align-items: center;
  justify-content: space-between;
  /* border: 1px solid red; */
}

.nav-left {
  padding-left: 80px;
  font-size: 34px;
  font-weight: 500;
  font-variant: small-caps;
}

.nav-right ul li {
  display: inline;
  margin: 0 48px 0 0;

  font-size: 20px;
}

.nav-right ul li:hover {
  text-decoration: underline;
}

.navbar-container a {
  text-decoration: none;
  color: black;
}

.title-container {
  width: 572px;
  height: fit-content;
  margin: 60px 0 24px 60px;
}

.home-title {
  font-size: 45px;
  font-weight: 700;
}

.home-desc {
  font-size: 18px;
  color: #626262;
}

.projects-container {
  grid-template-columns: 50% 50%;
  margin: 64px 60px;
}

.project-item-container {
  margin: 0 17.5px 35px 17.5px;
  transition: all 0.1s ease;
  border-radius: 5px;
  padding-bottom: 1em;
}

.project-item {
  justify-content: flex-start;
  flex-direction: column;
  /* padding: 5px; */
  width: auto;
  height: auto;
}

.project-img {
  width: 100%;
  background-color: #fff;
  justify-content: center;
  border-radius: 5px;
  overflow: hidden;
  /* border: 2px solid #1f1f1f; */
}

.project-img img {
	border-radius: 5px;
  height: 350px;
}

.item-title {
  margin: 20px 0 0 0;
  font-size: 20px;
  font-weight: 500;
}

.item-desc {
  color: #626262;
}

.footer-container {
  height: 200px;
  box-shadow: 0px -5px 20px #1e1e1e;
  margin: 128px 0 0 0;
}

.footer-title {
  position: relative;
  top: 52px;
  left: 80px;
  font-size: 24px;
}

.footer-links-container {
  position: relative;
  top: 100px;
  left: 80px;
  margin: 0 10px 0 0;
  color: black;
}

.footer-svg {
  margin: 0 30px 0 0;
}

.error-page {
  height: calc(100vh - 280px);
  text-align: center;
  font-size: 24px;
}

.about-container {
  flex-direction: row;
}

.about-contents {
  width: 800px;
  height: fit-content;
  margin: 60px 0 0 60px;

  /* border: 2px solid red; */
}

.generic-title {
  font-size: 45px;
  font-weight: 700;
}

.generic-subheading {
  margin: 18px 0;
  font-size: 24px;
  color: #828282;
}

.generic-body {
  font-size: 18px;
  line-height: 250%;
}

.about-body ul {
  list-style-type: disc;
  list-style-position: inside;
}

.about-svg {
  position: relative;
  width: fit-content;
  height: fit-content;
  top: 100px;
  right: -50px;
}

.contact-contents {
  width: 800px;
  height: fit-content;
  margin: 60px 0 0 60px;
}

.generic-body a:hover {
  text-decoration: underline;
}

.overlay {
  position: absolute;
  background-color: rgba(0,0,0,0.5);
	border-radius: 5px;
  opacity: 0;
  width: 664.6px;
  height: 464.43px;
}

.project-item:hover .overlay{
	opacity: 0.4;
	border: 2px solid black;
	transition: all 0.1s ease;
}

.project-link-tag {
  width: fit-content;
  height: fit-content;
}
